import React from 'react';
import {Link} from 'react-router-dom';

const Navbar = ()=>{
    return(
        <nav>
            <ul>
                <li>
                    <Link to="/">Home</Link>
                </li>
            </ul>
            <ul>
                <li>
                    <Link to="/character">Character</Link>
                </li>
            </ul>
            <ul>
                <li>
                    <Link to="/about">About</Link>
                </li>
            </ul>
        </nav>
    )
}

export default Navbar;