import React, { useState } from 'react';

const SingleComic = ({comic})=>{
    return(
        <>
        <div className='card'>
            <img className="img-hover" src={comic.thumbnail.path+'/landscape_medium.jpg'} alt="" />
            <span style={{color:"#c59d5f",fontWeight:"bold",fontFamily:"sans-serif",letterSpacing:'.1rem'}}>{comic.title}</span>
        </div>
        </>
    );
}

export default SingleComic;