import React,{useState,useEffect} from 'react';
import md5 from 'md5';
import SingleComic from './extras/SingleComic.js';

const private_key = '887b9b6133997756e7409adb53b208f031cf94c0';
const public_key = '1c77b470480f620a75b43352f5caeec3';
const start_url = 'https://gateway.marvel.com/v1/public/characters?name=';
let ts = new Date().getTime();
const hash = md5(ts+private_key+public_key);

const Home = ()=>{
    const [iserror,setError] = useState(false);
    const [character,setCharacter] = useState('');
    const [characterid,setCharacterid] = useState('');
    const [comics,setComics] = useState([]);
    const handleSubmit = (e)=>{
        e.preventDefault();
        const id_url = start_url+character+'&ts='+ts+'&apikey='+public_key+'&hash='+hash;
        const hero_id = fetch(id_url).then(response => response.json()).then(data => {
            if(data.data.count!==0){
                console.log(data.data.count);
                let id = data.data.results[0].id;
                setCharacterid(id);
                setError(false);
            }
            else{
                console.log(data);
                setError(true);
            }
        });
    }
    useEffect(()=>{
        if (characterid) {
            const comic_url = "https://gateway.marvel.com:443/v1/public/series?characters="+characterid+'&ts='+ts+'&apikey='+public_key+'&hash='+hash;
            fetch(comic_url).then(response => response.json()).then(data => {
                const result = data.data.results;
                setComics(result);
                console.log(result);
            });
        }
    },[characterid]);
    if(iserror){
        return(
            <>
            <div style={{textAlign:"center"}}>
            <h3 style={{color:"#fff"}}>
                Error in loading page
            </h3>
            <span style={{color:"#fff"}}>please try using - symbols between names eg:Spider-Man</span>
            </div>

            </>
        );
    }
    return(
        <>
        <div className='container'>
            <h2>Explore Marvel Universe</h2>
            <form className='form'>
                <p>Avenger</p>
                <input type="text" name="heroname" id="hero" value={character} onChange={(event)=>{
                    setCharacter(event.target.value);
                }} />
                <button className='btn' type='submit' onClick={handleSubmit}>Search</button>
            </form>
            <h3 style={{color:"#e9b05b"}}>Top 20 Comics Casting {character}</h3>
            <div className='grid'>
            {comics.map((comic)=>{
                return(
                    <>
                    <SingleComic key={comic.id} comic={comic}/>
                    </>
                )
            })}
            </div>
        </div>
        </>
    )
}

export default Home;