import React, { useEffect, useState } from 'react';
import md5 from 'md5';

const private_key = '887b9b6133997756e7409adb53b208f031cf94c0';
const public_key = '1c77b470480f620a75b43352f5caeec3';
const start_url = 'https://gateway.marvel.com/v1/public/characters?name=';
let ts = new Date().getTime();
const hash = md5(ts+private_key+public_key);
const Character = ()=>{
    const [onecharacter,setOnecharacter] = useState('');
    const [data,setData] = useState([]);
    const handleClick = (e)=>{
      e.preventDefault();
      const id_url = start_url+onecharacter+'&ts='+ts+'&apikey='+public_key+'&hash='+hash;
      fetch(id_url).then(response => response.json()).then(data => {
        setData(data.data.results);
      });
    }
    return(
      <>
        <div className='container'>
            <h2>Avengers portal</h2>
            <form className='form'>
                <p>Avenger</p>
                <input type="text" name="heroname" id="hero" value={onecharacter} onChange={(event)=>{
                    setOnecharacter(event.target.value);
                }} />
                <button className="btn" type="submit" onClick={handleClick}>Avengers Assemble</button>
            </form>
          {
            data.map((dat,index)=>{
              return(
                <div className="avenger" key={index}>
                    <img src={dat.thumbnail.path + "/landscape_medium.jpg"} alt="" />
                    <h3 style={{fontFamily:"sans-serif",fontWeight:"bolder",letterSpacing:'.2rem'}}>{dat.name}</h3>
                    <p style={{letterSpacing:".1rem",}}>{dat.description}</p>
                </div>
              )
            })
          }
        </div>
      </>  
    );
}

export default Character;